import { Component, OnInit } from '@angular/core';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { Cjenovnik } from 'src/app/model/priceList';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { PriceListService } from 'src/app/services/price-list.service';

@Component({
  selector: 'app-price-list',
  templateUrl: './price-list.component.html',
  styleUrls: ['./price-list.component.css']
})
export class PriceListComponent implements OnInit {

  modal: NgbModalRef;
  cjenovnik: Cjenovnik;
  cjenovnici: Cjenovnik[] = [];
  role: string='';
  username: string='';
  mode: string = 'ADD';

  constructor(private router: Router, private toaster: ToastrService,
    private route: ActivatedRoute, private ngbModal: NgbModal,
    private priceListService: PriceListService) {
      this.cjenovnik = {
        cijenaPoDanu:0,
        cijenaPoKM:0,
        cijenaCDW:0,
        popust:0,
        id:null,
        userUsername: localStorage.getItem('username')
      }
     }

  ngOnInit(): void {
    this.username = localStorage.getItem('username');
    this.role = localStorage.getItem('user-role');

    this.priceListService.getCjenovnik(this.username).subscribe(
      data => {
        this.cjenovnici = data;
      },
      error => {
        this.toaster.error('Greska prilikom dobavljanja cjenovnika');
      }
    );
  }

  refresh(){
    this.priceListService.getCjenovnik(this.username).subscribe(
      data => {
        this.cjenovnici = data;
      },
      error => {
        this.toaster.error('Greska prilikom dobavljanja cjenovnika');
      }
    );
  }

  dodajCjenovnik(sadrzaj: any){
    this.clear();
    //this.refresh();
    this.mode = 'ADD';
    this.modal = this.ngbModal.open(sadrzaj,{size: 'lg'});
  }

  clear(){
    this.cjenovnik.id = null;
    this.cjenovnik.cijenaPoDanu = 0;
    this.cjenovnik.cijenaPoKM = 0;
    this.cjenovnik.cijenaCDW = 0;
    this.cjenovnik.popust = 0;
    this.cjenovnik.userUsername = this.username;
  }

  dodaj(){
    if(this.cjenovnik.cijenaPoDanu == 0 || this.cjenovnik.cijenaPoKM == 0 || this.cjenovnik.cijenaCDW == 0 ){
      this.toaster.info("Ispunite polja");
      return;
    }

    if(this.cjenovnik.popust >=100){
      this.toaster.info("Popust ne moze biti veci od 99%");
      return;
    }

      this.priceListService.dodajCjenovnik(this.cjenovnik).subscribe(
        data => {
          this.toaster.success("Cjenovnik uspjesno kreiran");
          this.clear();
          this.refresh();
          this.modal.close();
        },
        error => {
            this.toaster.error("Greska prilikom kreiranja cjenovnika");
        }
      );
  }

  izmjeniCjenovnik(sadrzaj: any, cjenovnik: Cjenovnik){
    this.cjenovnik = cjenovnik;
    this.mode = 'EDIT';
    this.modal = this.ngbModal.open(sadrzaj,{size: 'lg'});
  }

  sacuvaj(){
    if(this.cjenovnik.cijenaPoDanu == 0 || this.cjenovnik.cijenaPoKM == 0 || this.cjenovnik.cijenaCDW == 0 ){
      this.toaster.info("Ispunite polja");
      return;
    }
      this.priceListService.izmjeniCjenovnik(this.cjenovnik).subscribe(
        data => {
          this.toaster.success("Cjenovnik uspjesno izmjenjen");
          this.clear();
          this.refresh();
          this.modal.close();
          this.mode = 'ADD';
        },
        error => {
            this.toaster.error("Greska prilikom izmjene cjenovnika");
        }
      )
      
  }

}
